import { Base, Type, markType } from "igniteui-core/type";

/**
 * @hidden 
 */
export class ComponentBoolValueChangedEventArgs extends Base {
	static $t: Type = markType(ComponentBoolValueChangedEventArgs, 'ComponentBoolValueChangedEventArgs');
	b: boolean = false;
	static a(a: any): ComponentBoolValueChangedEventArgs {
		return ((() => {
			let $ret = new ComponentBoolValueChangedEventArgs();
			$ret.b = <boolean>a;
			return $ret;
		})());
	}
}


